import React, { useState, useRef, useEffect } from 'react';
import Link from 'next/link';
import { connect } from 'react-redux';
import '../src/styles/home.scss';
import { HeroSection } from '../src/components/homes/slider';
import ServiceItem from '../src/components/homes/service_item';
import TopNews from '../src/components/shares/top_news';
import { ArrowRightIcon } from '../src/components/icons';
import { State } from '../src/reducers';
import EventListener from 'react-event-listener';

interface HomeProps {}

const Home: React.FC<HomeProps & State> = props => {
  const [Services] = useState([
    {
      image_src:
        'https://0401morita.github.io/horiguchi-seni/static/images/service/machine-quilting.jpg',
      title: '縫製・販売',
      summary: '生地・デザインの選定からユニフォームを縫製・販売',
      label_en: 'Sewing & Sales',
      label_ja: '縫製・販売について',
      link: '/service/sewing'
    },
    {
      image_src:
        'https://0401morita.github.io/horiguchi-seni/static/images/service/name.jpg',
      title: 'ユニフォーム販売',
      summary: '各種メーカー品を特別価格にて販売',
      label_en: 'Sale',
      label_ja: '販売について',
      link: '/service/sale'
    },
    {
      image_src:
        'https://0401morita.github.io/horiguchi-seni/static/images/service/iron.jpg',
      title: 'リネンサプライ',
      summary: 'ユニフォームのリースやクリーニング付リース',
      label_en: 'Linen supply',
      label_ja: 'リネンサプライについて',
      link: '/service/linen-supply'
    }
  ]);

  const [windowState, setWindowState] = useState({
    width: 0,
    height: 0,
    vw: 0
  });

  const homeMessageSectionEl = useRef<HTMLDivElement>(null);
  const primaryMessageEl = useRef<HTMLDivElement>(null);
  /*
   * リサイズ実行関数
   *
   * @event {Object} event - イベント
   */
  const handleResize = (e: any) => {
    console.log('resize');
    let timer = 200;
    if (timer > 0) {
      clearTimeout(timer);
    }

    timer = setTimeout(function() {
      var test = {
        width: window.innerWidth,
        height: window.innerHeight,
        vw: Math.floor(window.innerWidth * 0.01)
      };

      setWindowState(prevState => {
        console.log({ ...prevState, ...test });
        return { ...prevState, ...test };
      });

      console.log(windowState, test);
    }, timer);
  };
  /*
   * スクロール実行関数
   *
   * @event {Object} event - イベント
   */
  const handleScroll = () => {
    const scrollTop = Math.max(
      window.pageYOffset,
      document.documentElement.scrollTop,
      document.body.scrollTop
    );

    if (primaryMessageEl.current && homeMessageSectionEl.current) {
      const { width: windowWidth, height: windowHeight, vw } = windowState;
      let fixPosition = 0;
      let scrollBottom = 0;
      const homeMessageSectionTop = homeMessageSectionEl.current.offsetTop;
      if (windowWidth >= 768) {
        scrollBottom = scrollTop + window.innerHeight;
      } else {
        scrollBottom = scrollTop + window.innerHeight;
      }

      if (fixPosition < scrollBottom) {
        document.body.classList.add('fix-message');
      } else {
        document.body.classList.remove('fix-message');
      }
    }
  };

  useEffect(() => {
    window.addEventListener('load', handleResize, false);
    window.addEventListener('resize', handleResize, false);

    if (primaryMessageEl.current) {
      console.log(primaryMessageEl.current, windowState);
    }

    document.body.classList.add('in');

    var userAgent = navigator.userAgent;
    if (
      userAgent.indexOf('iPhone') >= 0 ||
      userAgent.indexOf('iPad') >= 0 ||
      userAgent.indexOf('Android') >= 0
    ) {
      window.addEventListener('orientationchange', handleResize);
    } else {
      window.addEventListener('resize', handleResize, false);
    }

    window.addEventListener('load', handleScroll, false);
    window.addEventListener('scroll', handleScroll, false);
    window.addEventListener('touchmove', handleScroll, false);
    return () => {
      window.removeEventListener('load', handleScroll, false);
      window.removeEventListener('scroll', handleScroll, false);
      window.removeEventListener('touchmove', handleScroll, false);
      window.removeEventListener('resize', handleResize, false);
      window.removeEventListener('orientationchange', handleResize);
    };
  }, []);

  return (
    <React.Fragment>
      <EventListener target="window" onResize={handleResize} />

      <HeroSection />
      <main className="mainContainer">
        <div className="mainContainer__index">Company - 会社概要</div>
        <div className="content mainContainer__content">
          <section className="homeMessage">
            <div className="homeMessage__primary">
              <div className="primaryMessage" ref={primaryMessageEl}>
                <h2 className="primaryMessage__title">
                  <span>ENJOY</span>
                  <br />
                  <span>UNIFORM</span>
                </h2>
                <p className="primaryMessage__description">
                  <span>ユニフォームを着る人をもっと快適に。</span>
                  <br />
                  <span>もっと楽しく。</span>
                </p>
              </div>
            </div>
            <div className="homeMessage__secondary">
              <div className="secondaryMessage">
                <p>堀口繊維工業は、戦後まもなく縫製事業を立ち上げました。</p>
                <p>
                  ユニフォームを縫製・販売する一方で平成に入りクリーニング設備を
                  整えました。
                </p>
                <p>
                  現在、ユニフォームの縫製・販売・クリーニング・クリーニング付リース（リネンサプライ）を
                  中心に展開いたしております。
                </p>

                <a className="read-more-link">
                  <span className="en">About</span>
                  <span className="ja">堀口繊維工業について</span>
                </a>
              </div>
            </div>
          </section>

          <div className="contentInnerHeader">
            <h3 className="contentInnerHeader__title">
              <span className="ja">私たちの事業</span>
              <span className="en">Service</span>
            </h3>
          </div>

          <div className="service-list">
            {Services.map((service, index) => {
              return (
                <ServiceItem
                  {...{ ...service, index: '0' + (index + 1) }}
                  key={index}
                />
              );
            })}
          </div>

          <div className="contentFooter">
            <Link href="/contact">
              <a className="btn btn-black contentFooter__button">
                お問い合わせ
                <ArrowRightIcon />
              </a>
            </Link>
          </div>
        </div>
      </main>
    </React.Fragment>
  );
};

export default connect(state => state)(Home);
